/* resets and general styles */

* {
  margin: 0 auto;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

::-moz-selection {
  color: #ffffff;
  background: #ff22a3;
}

::selection {
  color: #ffffff;
  background: #ff229c;
}

body {
  font: normal 16px 'Roboto', sans-serif;
  position: absolute;
  height: 100%;
  width: 100%;
}

div.wrapper {
  width: calc(4 * 225px);
  margin: 0 auto;
}

span,
a,
strong {
  font-weight: 700;
}
a {
  transition: 0.2s;
  text-decoration: none;
  color: #787878;
  border-bottom: 1px solid #607d8b;
}
a:hover {
  transition: 0.2s;
  color: #ff22e22c;
}

/* page layout */

/* header */

header {
  width: 100%;
  height: 50%;
  background: #fff;
}

/* section */

section {
  width: 100%;
  height: 50%;
}

section h1,
section p.meta {
  color: #b2b2b2;
  margin-bottom: 15px;
  text-align: center;
}

section hgroup {
  margin-top: -360px;
}

section p.numbers {
  font-size: 6em;
}
section p.meta,
section p.strings {
  font-size: 1.5em;
}
section h1 {
  font-size: 4.5em;
}

/* countdown styles */

div#timer-1 {
  color: #fff;
}

div#timer-1 p {
  width: 100%;
  display: inline-block;
  text-align: center;
}

p.numbers {
  width: 100%;
  height: 85%;
  background: #ff0000;
  margin-top: -25px;
  padding-top: 100px;
}

p.strings {
  width: 100%;
  height: auto;
  padding: 30px 0;
  background: #ff227e;
  color: #fff;
}

div.field {
  width: 210px;
  height: 360px;
  background: #44463f;
  float: left;
  padding: 25px 0 0 0;
  margin: 30px 15px 0 0;
  -webkit-box-shadow: 5px 6px 9px 1px rgba(53, 53, 53, 0.5);
  -moz-box-shadow: 5px 6px 9px 1px rgba(53, 53, 53, 0.5);
  box-shadow: 5px 6px 9px 1px rgba(53, 53, 53, 0.5);
}

/* footer */

footer {
  position: relative;
  top: 60px;
}

footer p {
  color: #787878;
}
